<template>
    <div id="tree">
        <el-row>
            <el-col :span="24">
                <el-card shadow="always" class="card" ref="card" @click.native="cancleHandle()">
                    <div id="chart-container"></div>
                    <div class="handleMenu" ref="handleMenu">
                        <span class="edit" ref="cur1" @click="handleEdit(handleData)">编辑项目信息</span>
                        <span class="resolve" ref="cur2" @click="handleResolve(handleData)" v-show="flag">分解项目节点</span>
                        <span class="delete" ref="cur3" @click="handleDelete(handleData)">删除项目节点</span>
                    </div>
                </el-card>
            </el-col>
        </el-row>
        <el-dialog
        class="delTips"
        title="谨慎操作提示"
        :visible.sync="dialogDelete"
        top="30vh"
        width="300px">
            <el-dialog
            class="delTips"
            width="300px"
            title="请输入登录密码以确认"
            :visible.sync="dialogPassword"
            top="30vh"
            append-to-body>
                <div class="psw">
                    <el-input type="password" id="userPsw" v-model="userPassword" placeholder="请输入您的登录密码"></el-input>
                    <span @click="showPsw()" id="eye" class="iconfont icon-guanbixianshi"></span>
                </div>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogPassword = false">取 消</el-button>
                    <el-button type="primary" @click="confirm()">确 认</el-button>
                </span>
            </el-dialog>
            <span>你确定要删除“<b>{{del.title}}</b>”此项目吗？其子项目也将会随之删除！</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogDelete = false">取 消</el-button>
                <el-button type="primary" @click="dele()">删 除</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import store from '../store/store.js'
import axios from 'axios';
import { type } from 'os';
export default {
    name: 'tree',
    inject:["reload"],
    data(){
        return{
            watchProgress: '',
            'flag': true,
            watchFlag: '',
            dialogDelete: false,
            dialogPassword: false,
            userPassword: '',       //用户密码
            level: '2',
            brother: '1',       //当前项目的兄弟节点  用来区分兄弟项目和进行操作
            treedata: {
                // title: '提高教育质量，加强学生全面发展',
                // name: '李老师',
                // level: '1',
                // id: '1',
                // brother: '1',
                // className: 'firstLevel',
                // children: [{
                //     name: '周老师',
                //     title: '提高学生的身体素质',
                //     level: '2',
                //     id: '2',
                //     brother: '1',
                //     className: 'secondLevel',
                //     children: [{
                //         name: '吴老师',
                //         title: '学生体质测评合格率大于80%',
                //         level: '3',
                //         id: '5',
                //         brother: '1',
                //         className: 'thirdLevel',
                //     },{
                //         name: '孙老师',
                //         title: '举办学校秋季运动会',
                //         level: '3',
                //         id: '6',
                //         brother: '2',
                //         className: 'thirdLevel',
                //         children: [{
                //             name: '马老师',
                //             title: '开始运动会筹备</br>',
                //             level: '4',
                //             id: '16',
                //             brother: '1',
                //             className: 'fourthLevel',
                //         },{
                //             name: '钱老师',
                //             title: '开幕式排练</br>',
                //             level: '4',
                //             id: '17',
                //             brother: '2',
                //             className: 'fourthLevel',
                //         },{
                //             name: '王老师',
                //             title: '确定裁判组/工作</br>组',
                //             level: '4',
                //             id: '18',
                //             brother: '3',
                //             className: 'fourthLevel',
                //         },{
                //             name: '张老师',
                //             title: '比赛项目报名</br>',
                //             level: '4',
                //             id: '19',
                //             brother: '4',
                //             className: 'fourthLevel',
                //         },{
                //             name: '唐老师',
                //             title: '奖品设置与购买</br>',
                //             level: '4',
                //             id: '20',
                //             brother: '5',
                //             className: 'fourthLevel',
                //         },{
                //             name: '田老师',
                //             title: '落实好急救措施</br>',
                //             level: '4',
                //             id: '21',
                //             brother: '6',
                //             className: 'fourthLevel',
                //         },{
                //             name: '孙老师',
                //             title: '组织学生参加</br>',
                //             level: '4',
                //             id: '22',
                //             brother: '7',
                //             className: 'fourthLevel',
                //         },{
                //             name: '吴老师',
                //             title: '指定比赛日程</br>',
                //             level: '4',
                //             id: '23',
                //             brother: '8',
                //             className: 'fourthLevel',
                //         }]
                //     },{
                //         name: '周老师',
                //         title: '提高学生的身体素质',
                //         level: '3',
                //         id: '2',
                //         brother: '3',
                //         className: 'thirdLevel',
                //     }]
                // }]
            },
            handleData: null,
            resolve: {
                title: '分解项目节点',
                level: '2',
                id: '1',
                mc:'项目名称项目名称',
                important: '',
                leader: '',
            },
            del: {
                title: '',
                level: '',
                id: '',
            },
        }
    },
    methods:{
        handleNodeClick(data) {
            console.log(data);
        },
        handleEdit(data){       //编辑
            console.log(data);
            console.log(this.$route.query)
            localStorage.setItem("data",data)
            // 项目树id
            // localStorage.setItem("userId", data.id);
            // axios.post("http://192.168.31.104/ydjxzg/mb/goEditTarget.action",{
            //     "targetid":data.id
            // })
            // .then(resp=>{
            //     if(200==resp.data.status){
            //         console.log("成功")
            //     }else{
            //         console.log("失败")
            //     }
            // })
            console.log(data.level);
            console.log(this.level)
            var fid=this.$route.query.id;
            var fdj=this.$route.query.dj;
            var userId = window.sessionStorage.getItem('user_id')
            var userdj = window.sessionStorage.getItem('userdj')
            console.log(userdj)
            console.log(userId)
            console.log(data.creator_id)
            if(userdj == 1 && this.user_id == data.manager_id && data.target_dj == 1){   //用户只能操作自己的项目不能操作兄弟节点项目
                this.$router.push({
                    path: '/edit',
                    name: 'edit',
                    query: {
                        level: data.level,
                        id: data.id,
                        fid:fid,
                        fdj:fdj,
                    }
                })
            }else if(userId == data.creator_id){
                this.$router.push({
                    path: '/edit',
                    name: 'edit',
                    query: {
                        level: data.level,
                        id: data.id,
                        fid:fid,
                        fdj:fdj,
                    }
                })
            }else{          //用户level小于项目节点level不能右键不能操作
                this.$message({
                    message: '您不能修改上级分解给您的目标!',
                    type: 'warning'
                });
            }
            
        },
        handleResolve(data){        //分解
            console.log(data)
            console.log(data.level)
            console.log(this.level)
            console.log(this.$route.query)
            var fid=this.$route.query.id;
            var fdj=this.$route.query.dj;
            console.log(fid+fdj)
            var userId = window.sessionStorage.getItem('user_id')
            console.log(userId)
            var aa = sessionStorage.getItem('shifoufenjie');
            // if(data.target_dj==4 && userId==data.manager_id){
            //     this.$message({
            //         message: '此项目不能分解!',
            //         type: 'warning'
            //     });
            // }
            if(data.target_dj==4){
                this.$message({
                    message: '此项目不能分解!',
                    type: 'warning'
                });
            }
            else if(userId==data.manager_id){   //用户只能操作自己的项目不能操作兄弟节点项目
                this.$router.push({
                    path: '/resolve',
                    name: 'resolve',
                    query: {
                        id: data.id,
                        dj:data.target_dj,
                        fid:fid,
                        fdj:fdj,
                    }
                })
            }
            // else if(data.level - this.level >= 1){
            //     this.$router.push({
            //         path: '/resolve',
            //         name: 'resolve',
            //         query: {
            //             level: data.level,
            //             id: data.id,
            //         }
            //     })
            //}
            else{          //用户level小于项目节点level不能右键不能操作
                this.$message({
                    message: '您没有权限分解非您负责的项目！',
                    type: 'warning'
                });
            }
        },
        handleDelete(data){     //删除
            console.log(data)
            localStorage.setItem("id",data.id)
            localStorage.setItem("dj",data.target_dj)
            console.log(localStorage.getItem('id'), 'ididididididid');
            console.log(localStorage.getItem('dj'));
            var userId = window.sessionStorage.getItem('user_id')
           
           if(userId==data.creator_id){        //用户level不能越级分解项目节点level
                if(data.target_name){
                    var str = data.target_name.substr(8,12)
                    console.log(str)
                }
                this.del.title = data.title
                this.del.level = data.level
                this.del.id = data.id
                this.dialogDelete = true
            }
            // else if(this.level == data.level && this.brother == data.brother){   //用户只能操作自己的项目不能操作兄弟节点项目
            //     if(data.title){
            //         var br = "</br>"
            //         if(data.title.indexOf("</br>") != '-1'){
            //             var Dtitle = data.title.replace("</br>","")
            //         }
            //     }
            //     if(Dtitle){
            //         this.del.title = Dtitle
            //     }else{
            //         this.del.title = data.title
            //     }
            //     this.del.level = data.level
            //     this.del.id = data.id
            //     this.dialogDelete = true
            // }
            else{          //用户level小于项目节点level不能右键不能操作
                this.$message({
                    message: '您没有权限操作此项目!',
                    type: 'warning'
                });
            }
        },
        cancleHandle(){
            this.$refs.handleMenu.style.display = 'none'
        },
        dele(){
            console.log(this.del)
            //根据this.del的level和id向后台发起删除请求，完成后刷新页面
            this.dialogPassword = true
            // var _this = this
            // var id = localStorage.getItem("id");
            // var dj = localStorage.getItem("dj");
            // $.ajax({
            //     type: "post",
            //     url: _this.baseUrl + "/ydjxzg/mb/deleteTarger.action",
            //     data: {
            //         targetid:id,
            //         targetdj:dj
            //     },
            //     async: false,
            //     dataType: "json",
            //     success: function (res) {
            //         console.log(res)
            //         _this.$router.push({path: "/zgmb"})
            //     }
            // })
            // this.dialogDelete = false
            // this.$message({
            //     message: '删除成功!',
            //     type: 'success'
            // })
            // this.$router.go(0)
        },
        showPsw(){
            if($("#eye").attr("class") == "iconfont icon-guanbixianshi"){
                $("#eye").removeClass("icon-guanbixianshi").addClass("icon-xianshi")
                $("#userPsw").attr("type","text")
            }else{
                $("#eye").removeClass("icon-xianshi").addClass("icon-guanbixianshi")
                $("#userPsw").attr("type","password")
            }
        },
        confirm(){
            if(this.userPassword == '123456'){       //向后台发起请求判断密码是否正确
                this.dialogDelete = false
            var _this = this
            var id = localStorage.getItem("id");
            var dj = localStorage.getItem("dj");
            localStorage.setItem('dj', dj);
            // var dj = this.$route.query.dj
            $.ajax({
                type: "post",
                url: _this.baseUrl + "/ydjxzg/mb/deleteTarger.action",
                data: {
                    targetid:id,
                    targetdj:dj
                },
                async: false,
                dataType: "json",
                success: function (res) {
                    console.log(res)
                    _this.$message.success('删除成功!');
                    _this.dialogPassword = false;
                    var dj00 = localStorage.getItem("dj");
                    if (dj00 == 1) {
                        _this.$router.push({path: "/zgmb", query: {shuaxin: 1}});
                    }else{
                        _this.reload();
                    }
                    // _this.$message.success('删除成功!');
                    // _this.reload()
                    // _this.init();
                    
                }
            })
                // this.$message({
                //     message: '删除成功!',
                //     type: 'success'
                // })
                //this.$router.go(0)
            }else if(this.userPassword == ''){
                this.$message({
                    message: '密码不能为空!',
                    type: 'warning'
                })
            }else{
                this.$message({
                    message: '密码错误!',
                    type: 'warning'
                })
            }
        },
        init(){
            console.log(this.$route.query)  //使用level和id向后台发起请求获取数据赋值给treedata
            var id = this.$route.query.id
            var dj = this.$route.query.dj
            console.log('等级'+dj)
            var _this = this
            $.ajax({
                type: "post",
                url: _this.baseUrl + "/ydjxzg/mb/projectTree.action",
                data: {'targetid':id},
                async: false,
                dataType: "json",
                success: function (res) {
                    console.log(res)
                    if(res.projectTree){
                        res.projectTree.title = res.projectTree.target_name
                        _this.treedata = res.projectTree
                        console.log(_this.treedata)
                    }
                    if(res.targetOne){
                        _this.level = res.targetOne.level
                        _this.brother = res.targetOne.brother
                        console.log(_this.brother)
                    }
                }
            });
            //在init里获取数据
        }
    },
    mounted() {
        this.init();
        // console.log(this.$route.query)  //使用level和id向后台发起请求获取数据赋值给treedata
        // this.level = this.$route.query.level
        var _this = this
        console.log(_this.treedata)
        $('#chart-container').orgchart({
            'data': _this.treedata,
            'nodeContent': 'manager_name',
            'nodeTitle': 'title',
            'createNode': function($node,data){
                // console.log($node,data, '11111111')
                document.oncontextmenu = function(e){
                    return false;   //取消右键点击的默认事件
                };
                $node.mousedown(function(e) {
                    // console.log(data)
                    // console.log(e);
                    if (3 == e.which) {     //右键为3
                        var userId = window.sessionStorage.getItem('user_id')
                        _this.handleData = data
                        _this.$refs.handleMenu.style.display = 'block'
                        if(store.state.isCollapse){
                            _this.$refs.handleMenu.style.left = e.clientX - 235 + 'px'
                        }else{
                            _this.$refs.handleMenu.style.left = e.clientX - 105 + 'px'
                        }
                        _this.$refs.handleMenu.style.top = e.clientY - 90 + 'px'
                        // console.log(_this.level,data.level,data.brother,data.creator_id,data.manager_id);
                        _this.watchProgress = data.progress;
                        _this.watchFlag = data.level;
                        if(userId== data.creator_id && userId == data.manager_id ){       //用户level不能越两级操作项目节点level
                            _this.$refs.cur1.style.cursor = 'default'
                            _this.$refs.cur2.style.cursor = 'default'
                            _this.$refs.cur3.style.cursor = 'default'
                        }else if(userId== data.creator_id && userId !== data.manager_id){        //用户level不能越级分解项目节点level
                            _this.$refs.cur1.style.cursor = 'default'
                            _this.$refs.cur2.style.cursor = 'not-allowed'
                            _this.$refs.cur3.style.cursor = 'default'
                        }
                        else if(userId !== data.creator_id && userId == data.manager_id){

                            _this.$refs.cur1.style.cursor = 'not-allowed'
                            _this.$refs.cur2.style.cursor = 'default'
                            _this.$refs.cur3.style.cursor = 'not-allowed'
                        }
                        else{          //用户level小于项目节点level不能右键不能操作
                            _this.$refs.cur1.style.cursor = 'not-allowed'
                            _this.$refs.cur2.style.cursor = 'not-allowed'
                            _this.$refs.cur3.style.cursor = 'not-allowed'
                        }
                    } else if (1 == e.which) {      //左键为1
                        _this.$refs.handleMenu.style.display = 'none'
                    }
                })
                $node.dblclick(function(){
                    // console.log($node,data)
                    // console.log(data)
                    // console.log(data.level)
                    // console.log(_this.level)
                    // if(data.level - _this.level >= 1){       //用户只能查看下N级项目详情
                    //     _this.$router.push({
                    //         path: '/detail',
                    //         name: 'detail',
                    //         query: {
                    //             id: data.id
                    //         }
                    //     })
                    // }else if(_this.level == data.level && _this.brother == data.brother){   //用户只能查看自己的项目不能查看兄弟节点项目
                    //     _this.$router.push({
                    //         path: '/detail',
                    //         name: 'detail',
                    //         query: {
                    //             id: data.id
                    //         }
                    //     })
                    // }else{          //用户不能查看上级项目详情
                    //     _this.$message.warning({message: '您没有权限查看此项目！'})
                    // }
                    var fuid=localStorage.getItem("creid");
                    var userId = window.sessionStorage.getItem('user_id')
                    // console.log(data.id)
                    // console.log(fuid)
                    // console.log(userId)
                    // var fid = localStorage.getItem("fid")
                    if(fuid == userId){   //用户只能操作自己的项目不能操作兄弟节点项目
                        _this.$router.push({
                            path: '/detail',
                            name: 'detail',
                            query: {
                                id: data.id,
                                userid:userId
                            }
                        })
                    }else{          //用户level小于项目节点level不能右键不能操作
                        _this.$message({
                            message: '您没有权限操作此项目!',
                            type: 'warning'
                        });
                    }
                })
                var secondMenuIcon = $('<i>', {
                    'class': 'fa fa-info-circle second-menu-icon',
                    click: function() {
                        $(this).siblings('.second-menu').toggle("fast");
                    }
                });
                var secondMenu = '<div class="second-menu titleInfo"><p>'+ data.title +'</p></div>';
                $node.append(secondMenuIcon).append(secondMenu);
            }
        });
        $('.card').scroll(function(){
            _this.$refs.handleMenu.style.display = 'none'
        })
    },
    watch: {
        '$route':{
            handler(route){
                var _this = this
                // console.log(route,_this)
                if(route.name == 'tree'){
                     _this.reload()
                }
            },
            deep: true
        },
        watchFlag: function (val, old) {
            // console.log(val, 'watchFlag')
            if (val == undefined) {
                // console.log('ok')
            }else{
                if (val == 4) {
                    this.flag = false;
                }else{
                    this.flag = true;
                }
            }
        },
        watchProgress: function (val, old) {
            // console.log(val, 'watchProgress')
            if (val == undefined) {
                // console.log('ok')
            }else{
                if (val == 0) {
                    this.flag = true;
                }else{
                    this.flag = false;
                }
            }
            
        }
    },
}
</script>

<style>
#chart-container{
    overflow: scroll;
}
#tree{
    width: 100%;
    height: 100%;
    margin-top: 20px;
}
#tree .el-row{
    padding: 0 10px 10px;
    overflow: initial;
}
#tree .el-row .el-col-24 .el-card{
    overflow-x: initial;
}
#tree .el-row .el-col-24 .el-card__body{
    overflow-x: initial;
}
#tree .el-row .el-col-24 .title{
    height: 50px;
    line-height: 50px;
    text-align: center;
}
#tree .el-row .el-col-24 .title i{
    display: none;
}
#tree .el-row .el-col-24 .titleInfo{
    border-radius: 4px;
    font-size: 14px;
    text-align: left;
    top: -25px;
    padding: 5px 10px;
}
#tree .handleMenu{
    display: none;
    width: 150px;
    font-size: 14px;
    line-height: 40px;
    padding: 10px 0;
    background: #ffffff;
    border-radius: 4px;
    position: absolute;
    /* left: 0px;
    top: 0px; */
    box-shadow: 0px 0px 10px #666666;
    z-index: 1000;
}
#tree .handleMenu span{
    display: block;
    text-align: center;
    cursor: default;
}
#tree .handleMenu span:hover{
    background: #eeeeee;
}
.el-dialog .psw{
    position: relative;
}
.el-dialog #eye{
    position: absolute;
    right: 8px;
    top: 12px;
}
.delTips .el-dialog__body{
    padding-left: 40px;
    padding-right: 40px;
}
</style>


